.home {
  background-color: rgb(238, 236, 236);
  .home-inner {
    padding-left: 20px;
    width: 80%;
    margin: 0 auto;
    background-color: #fff;
    padding-bottom: 40px;
  }
  .home-inner .nav {
    width: 100%;
  }
  .home-inner .nav ul {
    width: 74%;

    display: flex;
    list-style: none;
    width: 600px;
    height: 70px;
    align-items: center;
  }
  .home-inner .nav ul li {
    float: left;
    margin-right: 20px;
  }
  .home-inner .nav a {
    color: #5b21fa;
    padding: 3px 4px;
  }

  .home-inner .nav a.active {
    background-color: #5b21fa;
    color: #fff;
    border-radius: 3px;
  }

  .home-inner .topic-list .listone li {
    margin-bottom: 10px;
    list-style: none;
    border-top: 1px solid rgb(238, 238, 238);
    height: 50px;
    line-height: 50px;
  }
  .left {
    float: left;
    display: flex;
    align-items: center;
  }
  .right {
    float: right;
  }
  .home-inner .topic-list img {
    width: 40px;
    height: 40px;
    border-radius: 4px;
    margin-right: 8px;
  }
  .time {
    float: right;
    margin-right: 20px;
  }
  .fenlei {
    font-size: 14px;
    padding: 0 3px;
    background-color: #ccc;
    border-radius: 4px;
    margin: 0 6px;
    height: 24px;
    line-height: 24px;
  }
  .tic {
    font-size: 16px;
    color: rgb(129, 129, 129);
    max-width: 70%;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    display: inline-block;
  }
  .ant-pagination {
    margin-left: 40px;
  }
}
