/* 样式要求使用 df  使用 sass 或者 less */
header {
  width: 100%;
  background: #4404f5;
  font-size: 14px;
  height: 60px;
  display: flex;
  align-items: center;
}
.header-inner {
  width: 80%;
  margin: 0 auto;
  display: flex;
  align-items: center;
}
.header-inner > span {
  color: #ccc;
  font-size: 16px;
  margin-left: 20px;
}
header img {
  width: 40px;
}
.logo {
  float: left;
}
.logo img {
  width: 50px;
  height: 50px;
}
.zhanghao {
  float: left;
  border: 0;
  width: 200px;
  border-radius: 10px;
  margin-left: 20px;
}
.logout,
.login {
  float: left;
  color: #ccc;
  text-decoration: underline;
  font-size: 16px;
  margin-left: 20px;
  cursor: pointer;
}
.touxiang {
  width: 40px;
  height: 40px;
  border-radius: 5px;
  float: left;
}
.ziliao img {
  width: 30px;
  height: 30px;
  float: left;
  border-radius: 5px;
  margin-left: 20px;
  margin-right: 5px;
}
.ziliao span {
  float: left;
  color: #ccc;
  font-size: 16px;
}
.ziliao {
  display: flex;
  align-items: center;
}
.weidu {
  color: #ccc;
  font-size: 16px;
  margin-left: 15px;
  margin-right: 20px;
}
.weidu:hover {
  color: #ccc;
}
.weidushu {
  margin-left: 20px;
  width: 16px;
  height: 25px;
  border-radius: 6px;
  background-color: #4d95c6;
  line-height: 25px;
  text-align: center;
  cursor: pointer;
}
.fabu a {
  color: #ccc;
  font-size: 16px;
}
.clearfix:after,
.clearfix:before {
  content: "";
  clear: both;
  overflow: hidden;
  display: block;
}
.topic {
  width: 100%;
  background-color: #e1e1e1;
  padding-top: 20px;
}
.topic-inner {
  width: 80%;
  margin: 0 auto;
  background-color: #fff;
  padding-right: 30px;
  padding-top: 30px;
  padding-left: 30px;
}
li {
  list-style: none;
}
.huifuda,
.huifuxiao {
  background-color: #3374de;
  border: none;
  color: #fff;
  margin: 10px;
  padding: 5px 15px;
  border-radius: 4px;
}
.textarea {
  width: 300px;
  height: 100px;
  resize: none;
  display: block;
}
.comment-box li {
}
.huifuxiao {
  display: block;
}
